@import "../../../styles/Colors.scss";
@import "../../../styles/Animations.scss";

.darkchat-container {
    position: relative;
    height: 100%;
    padding-top: 3%;

    background-color: #000000;

    .animation-slide {
        height: 100%;
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;

        &.right {
            animation: slideRight 0.5s cubic-bezier(0.19, 1, 0.22, 1);
        }

        &.left {
            animation: slideLeft 0.5s cubic-bezier(0.19, 1, 0.22, 1);
        }
    }

    .darkchat-header {
        display: flex;
        width: 90%;
        align-items: center;
        justify-content: space-between;

        margin-top: 3rem;
        padding: 0.5rem 0;

        border-bottom: 1px solid rgba(200, 200, 200, 0.3);

        .title {
            font-size: 30px;
            font-weight: 500;
            color: #ffffff;
        }

        svg {
            font-size: 35px;
            color: var(--phone-color-orange);

            cursor: pointer;
        }

        &.chat {
            svg {
                font-size: 30px;
            }

            .title {
                font-size: 25px;
            }
        }
    }

    .channels-body {
        width: 100%;

        display: flex;
        flex-direction: column;

        .channel {
            width: 90%;
            color: #ffffff;

            display: flex;
            align-items: center;
            justify-content: space-between;

            padding: 0.75rem 1.5rem;
            border-bottom: 1px solid var(--phone-color-border);

            cursor: pointer;
            transition: all 0.1s ease-in-out;

            &:hover {
                background-color: rgba(255, 255, 255, 0.075);
            }

            .channel-info {
                display: flex;
                flex-direction: column;
                gap: 0.25rem;

                .channel-name {
                    font-size: 22px;
                }

                .recent-message {
                    display: flex;
                    flex-direction: row;
                    align-items: center;
                    gap: 0.3rem;

                    font-size: 15px;
                    color: var(--phone-color-orange);

                    .members {
                        display: flex;
                        align-items: center;
                        gap: 0.25rem;

                        font-weight: 700;

                        svg {
                            font-size: 22px;
                        }
                    }
                }
            }

            .right {
                display: flex;
                flex-wrap: row;
                align-items: center;
                gap: 0.3rem;

                font-size: 15px;
                font-weight: 600;
                color: var(--phone-text-secondary);

                svg {
                    font-size: 20px;
                }
            }
        }
    }

    .chat-bottom {
        position: absolute;
        bottom: 1.5rem;

        width: 85%;
        padding: 1rem 2rem;
        padding-bottom: 2.5rem;

        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: center;
        gap: 1rem;

        border-top: 1px solid var(--phone-color-border);

        input {
            width: 100%;
            border-radius: 20px;
            padding: 0.75rem 1rem;
            font-size: 16px;

            border: none;

            background-color: #a6a6a7;

            &:focus {
                outline: none;
            }
        }

        svg {
            font-size: 40px;
            color: var(--phone-color-orange);

            cursor: pointer;
        }
    }

    .chat-wrapper {
        display: relative;

        display: flex;
        flex-direction: column-reverse;
        height: 70%;

        width: 87%;

        overflow-y: auto;
        overflow-x: hidden;

        scroll-behavior: smooth;

        &::-webkit-scrollbar {
            display: none;
        }

        .chat-body {
            width: 100%;

            display: flex;
            flex-direction: column;

            margin-top: 1rem;
            padding: 1rem 0;
            gap: 0.5rem;

            .message {
                font-size: 16px;
                font-weight: 300;
                width: auto;
                max-width: 70%;

                color: rgb(15, 20, 25);

                display: flex;
                flex-direction: column;
                gap: 0.5rem;

                .content {
                    padding: 0.75rem 1rem;
                    border-radius: 20px;
                    overflow-wrap: break-word;
                    font-size: 18px;

                    a {
                        color: var(--phone-color-blue);
                        cursor: pointer;
                    }
                }

                .attatchments {
                    img {
                        width: 100%;
                        border-radius: 15px;
                    }
                }

                .date {
                    padding: 0 0.5rem;
                    font-size: 13px;
                    font-weight: 300;
                    color: #687684;
                }

                .user {
                    margin-top: 0.5rem;
                    padding: 0 0.5rem;
                    font-size: 15px;

                    font-weight: 500;
                    color: #687684;
                }

                &.self {
                    margin-left: auto;

                    .date {
                        text-align: right;
                        margin-left: auto;
                    }

                    .content {
                        border-bottom-right-radius: 0;

                        text-align: right;
                        background-color: var(--phone-color-orange);

                        color: #ffffff;
                        overflow-wrap: break-word;
                    }
                }

                &.other {
                    margin-right: auto;

                    .content {
                        border-bottom-left-radius: 0;

                        background-color: #a6a6a7;
                        color: $black-primary;

                        &:not(img) + .other {
                            background-color: transparent;
                            padding: 0;
                        }
                    }

                    .date {
                        margin-right: auto;
                    }
                }
            }
        }
    }
}
