.notch-container {
    position: absolute;
    height: 2.25rem;

    display: flex;
    justify-content: center;

    width: 100%;
    top: 1.2rem;
    left: 0;
    right: 0;
    margin: 0 auto;

    z-index: 9999;
    pointer-events: none;
}

.notch-wrapper {
    height: 2.25rem;

    width: 30%;
    background-color: black;

    border-radius: 25px;
    cursor: pointer;

    position: relative;

    pointer-events: all;

    overflow: hidden;

    .notch {
        z-index: 999999;
        height: 100%;
        width: 100%;

        display: flex;
        justify-content: center;
        align-items: center;

        .notch-content {
            width: 100%;
            height: 100%;

            position: relative;

            &[data-expanded="true"] {
                .incoming-call {
                    padding: 2rem 1rem;
                }

                .timer {
                    padding: 2rem 1.5rem;
                }
            }

            > div {
                box-sizing: border-box;
            }

            .call {
                width: 100%;
                height: 100%;

                display: flex;
                align-items: center;
                justify-content: space-between;

                padding: 0 0.75rem;

                .left {
                    display: flex;
                    align-items: center;
                    gap: 0.75rem;

                    img {
                        width: 3.5rem;
                        height: 3.5rem;
                        border-radius: 50%;
                        object-fit: cover;
                    }

                    .user {
                        display: flex;
                        flex-direction: column;
                        gap: 0.1rem;

                        margin-top: 0.5rem;

                        .info {
                            color: #6f6f6f;
                            font-size: 14px;
                        }

                        .name {
                            font-weight: 400;
                            font-size: 18px;
                            color: #ffffff;
                        }

                        .elapsed {
                            color: var(--phone-color-green);
                            font-size: 13px;
                        }
                    }

                    &.green {
                        display: flex;
                        align-items: center;

                        color: var(--phone-color-green);
                        font-size: 13px;
                        font-weight: 500;

                        svg {
                            font-size: 18px;
                            margin-right: -0.4rem;
                        }
                    }
                }

                .right {
                    display: flex;
                    align-items: center;
                    gap: 0.75rem;

                    .button {
                        height: 3rem;
                        width: 3rem;

                        border-radius: 50%;

                        display: flex;
                        align-items: center;
                        justify-content: center;

                        svg {
                            font-size: 25px;
                            color: #ffffff;
                        }

                        &.red {
                            background-color: var(--phone-color-red);
                        }

                        &.green {
                            background-color: var(--phone-color-green);
                        }

                        &.black {
                            background-color: rgb(20, 20, 20);
                        }

                        &.rotate {
                            transform: rotate(135deg);
                        }

                        cursor: pointer;
                        transition: all 0.2s ease-in-out;

                        &:hover {
                            filter: brightness(0.75);
                        }
                    }
                }
            }

            .music {
                width: 100%;
                height: 100%;

                display: flex;
                align-items: center;
                justify-content: space-between;

                padding: 0 0.75rem;

                &.full {
                    display: flex;
                    flex-direction: column;
                    align-items: center;
                    justify-content: flex-start;
                    gap: 1rem;

                    padding: 2rem;
                    padding-top: 0;
                }

                .left {
                    display: flex;
                    align-items: center;
                    gap: 0.75rem;

                    img {
                        width: 1.4rem;
                        height: 1.4rem;
                        border-radius: 30%;
                        object-fit: cover;
                    }
                }

                .right {
                    canvas {
                        margin-right: -0.3rem;
                    }
                }

                .song {
                    display: flex;
                    flex-direction: row;
                    align-items: center;
                    justify-content: space-between;

                    width: 100%;

                    padding-top: 1.5rem;

                    .song-info {
                        display: flex;
                        flex-direction: row;
                        align-items: center;
                        gap: 0.75rem;

                        img {
                            width: 4rem;
                            height: 4rem;
                            border-radius: 30%;
                            object-fit: cover;
                        }

                        .info {
                            display: flex;
                            flex-direction: column;
                            gap: 0.1rem;

                            .name {
                                font-weight: 500;
                                font-size: 18px;
                                color: #ffffff;
                                line-height: 15px;
                            }

                            .artist {
                                color: #6f6f6f;
                                font-size: 16px;
                                font-weight: 500;
                            }
                        }
                    }
                }

                .music-slider {
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    gap: 0.5rem;

                    width: 100%;

                    input {
                        width: 100%;

                        // style it
                        -webkit-appearance: none;
                        appearance: none;
                        background: transparent;
                        outline: none;
                        -webkit-transition: 0.2s;
                        transition: opacity 0.2s;
                        border-radius: 20px;

                        &::-webkit-slider-runnable-track {
                            width: 100%;
                            height: 0.4rem;
                            cursor: pointer;
                        }

                        &::-webkit-slider-thumb {
                            -webkit-appearance: none;

                            height: 1.5rem;
                            width: 1.5rem;
                            cursor: pointer;
                        }

                        &:focus {
                            outline: none;
                        }
                    }

                    .duration {
                        display: flex;
                        flex-direction: row;
                        justify-content: space-between;
                        align-items: center;

                        font-size: 13px;
                        color: #ffffff;
                        font-weight: 400;

                        opacity: 0.8;
                    }
                }

                .music-controls {
                    display: flex;
                    align-items: center;
                    gap: 1.5rem;

                    margin-top: -0.25rem;

                    svg {
                        font-size: 38px;
                        color: #ffffff;

                        &.pause {
                            font-size: 45px;
                        }
                    }
                }
            }

            .timer {
                width: 100%;
                height: 100%;

                display: flex;
                flex-direction: row;
                align-items: center;
                justify-content: space-between;

                padding: 0 0.75rem;

                .left {
                    display: flex;
                    align-items: center;
                    gap: 0.75rem;

                    svg {
                        font-size: 23px;

                        color: var(--phone-color-orange);
                    }

                    .button {
                        height: 3.5rem;
                        width: 3.5rem;

                        border-radius: 50%;

                        display: flex;
                        align-items: center;
                        justify-content: center;

                        &:last-child {
                            svg {
                                font-size: 33px;
                                stroke-width: 10;
                            }
                        }

                        svg {
                            font-size: 30px;
                            color: #ffffff;
                            // stroke width
                            stroke-width: 20;
                        }

                        &.black {
                            background-color: rgb(20, 20, 20);
                        }

                        &.orange {
                            background-color: rgb(255, 157, 10, 0.3);

                            svg {
                                color: var(--phone-color-yellow);
                            }
                        }

                        cursor: pointer;
                        transition: all 0.2s ease-in-out;

                        &:hover {
                            filter: brightness(0.75);
                        }
                    }
                }

                .right {
                    display: flex;
                    align-items: flex-end;
                    gap: 0.3rem;

                    .label {
                        font-size: 18px;
                        margin-bottom: 0.5rem;
                        font-weight: 400;
                    }

                    .time {
                        font-size: 45px;
                        font-weight: 300;
                    }

                    font-size: 13px;
                    color: var(--phone-color-orange);

                    font-weight: 500;
                }
            }
        }
    }
}
