@import "./Colors.scss";
@import "./Animations.scss";

[data-theme="light"] {
    .music-player-container {
        background-color: rgba(200, 200, 200, 0.6);
    }
}

[data-theme="dark"] {
    .music-player-container {
        background-color: rgba(20, 20, 20, 0.7);
    }
}

.lockscreen {
    height: 100%;
    width: 100%;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;

    position: absolute;
    z-index: 95;

    overflow-y: auto;
    scroll-behavior: smooth;
    scroll-snap-type: y mandatory;

    background-position: center;
    background-size: cover;

    border-radius: 25px;
    top: 0;

    &::-webkit-scrollbar {
        display: none;
    }

    .top {
        position: relative;

        scroll-snap-align: center;

        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;

        padding-top: 15%;

        font-weight: 300;

        color: #ffffff;

        svg {
            font-size: 40px;
            margin-bottom: 1rem;
        }

        .time {
            font-size: 90px;
        }

        .date {
            font-size: 22px;
        }
    }

    .music-player-container {
        width: 85%;

        border-radius: 25px;
        padding: 1rem;
        padding-bottom: 0.75rem;
        margin-top: 1.5rem;

        position: relative;

        .blur {
            position: absolute;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            z-index: -1;

            backdrop-filter: blur(15px);
            border-radius: 25px;
        }

        .music-player-wrapper {
            display: flex;
            flex-direction: column;
            align-items: center;
            gap: 0.75rem;

            width: 100%;
        }

        .music-player-header {
            display: flex;
            flex-direction: row;
            align-items: center;
            justify-content: space-between;

            width: 100%;

            .music-player-song {
                display: flex;
                flex-direction: row;
                align-items: center;
                gap: 0.75rem;

                .artwork {
                    height: 4rem;
                    width: 4rem;
                    border-radius: 7px;
                }

                .music-player-info {
                    display: flex;
                    flex-direction: column;

                    .title {
                        font-weight: 500;
                        font-size: 17px;
                        color: var(--phone-text-primary);
                        line-height: 15px;
                    }

                    .artist {
                        margin-top: 0.1rem;
                        font-size: 16px;
                        opacity: 0.6;
                        color: var(--phone-text-primary);
                        font-weight: 500;
                    }
                }
            }

            canvas {
                margin-bottom: 1.5rem;
            }
        }

        .duration-slider {
            display: flex;
            align-items: center;
            justify-content: center;
            gap: 0.5rem;

            width: 100%;

            input {
                width: 100%;

                // style it
                -webkit-appearance: none;
                appearance: none;
                background: transparent;
                outline: none;
                -webkit-transition: 0.2s;
                transition: opacity 0.2s;
                border-radius: 20px;

                &::-webkit-slider-runnable-track {
                    width: 100%;
                    height: 0.5rem;
                    cursor: pointer;
                }

                &::-webkit-slider-thumb {
                    -webkit-appearance: none;

                    height: 1.5rem;
                    width: 1.5rem;
                    cursor: pointer;
                }

                &:focus {
                    outline: none;
                }
            }

            .duration {
                display: flex;
                flex-direction: row;
                justify-content: space-between;
                align-items: center;

                font-size: 13px;

                color: var(--phone-text-primary);

                font-weight: 500;

                opacity: 0.7;
            }
        }

        .music-player-actions {
            display: flex;
            flex-direction: row;
            align-items: center;
            justify-content: space-between;

            width: 65%;

            // margin-top: -0.5rem;

            svg {
                background-color: inherit;
                color: var(--phone-text-primary);
                padding: 0;

                font-size: 35px;
                cursor: pointer;
                transition: all 0.2s ease-in-out;

                &:hover {
                    transform: scale(1.1);
                    filter: brightness(0.7);
                }
            }

            .pause {
                svg {
                    font-size: 45px;
                    stroke-width: 20;
                }
            }
        }
    }

    .actions {
        display: flex;
        flex-direction: row;
        justify-content: space-between;

        margin-bottom: 3.5rem;
        z-index: 8;
        opacity: 1;

        width: 70%;

        transition: all 0.3s ease-in-out;
        cursor: pointer;

        &.hidden {
            opacity: 0;
        }

        svg {
            padding: 1rem;
            border-radius: 50%;
            font-size: 30px;
            background: rgba(0, 0, 0, 0.35);

            transition: all 0.2s ease-in-out;

            &:nth-child(1) {
                rotate: -45deg;
            }

            &:hover {
                filter: brightness(1.3);
                scale: 1.03;
            }

            &[data-active="true"] {
                background-color: #ffffff;
                color: #000000;
            }
        }
    }

    .pin-wrapper {
        position: relative;
        padding-top: 15%;

        backdrop-filter: blur(10px);
        background: rgba(0, 0, 0, 0.35);

        width: 100%;
        height: 100%;

        display: flex;
        align-items: center;
        justify-content: center;

        .pin {
            animation: slideDown 1s cubic-bezier(0.19, 1, 0.22, 1);

            width: 80%;
            margin-bottom: 5rem;

            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;

            font-weight: 300;

            color: #ffffff;

            .title {
                margin-top: 5rem;

                font-size: 24px;
                font-weight: 400;
            }

            .numbers {
                margin-top: 5rem;
                display: grid;
                grid-template-columns: repeat(3, 1fr);
                -ms-grid-columns: repeat(3, 1fr);

                gap: 2rem;

                div {
                    display: flex;
                    flex-direction: column;
                    align-items: center;
                    justify-content: center;

                    background: rgba(145, 145, 145, 0.2);

                    border-radius: 50%;
                    height: 5rem;
                    width: 5rem;

                    text-align: center;
                    font-size: 34px;
                    font-family: "Roboto";
                    font-weight: 400;

                    &:hover {
                        cursor: pointer;
                        background: rgba(145, 145, 145, 0.55);
                    }

                    span {
                        font-size: 12px;
                        font-weight: 400;
                        letter-spacing: 3px;
                    }
                }
            }

            .bottom {
                z-index: 1;
                margin-top: 6rem;

                font-size: 17px;

                &:hover {
                    cursor: pointer;
                }
            }
        }
    }
}
