@import "../../../../styles/Colors.scss";

.crypto-container {
    height: 100%;
    width: 100%;

    background-color: #000000;

    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 3rem;

    .crypto-header {
        display: flex;
        flex-direction: column;
        gap: 1rem;

        margin-top: 5rem;
        width: 80%;

        position: relative;

        color: #ffffff;
        font-size: 22px;
        font-weight: 500;

        .data {
            position: absolute;
            top: 2.5rem;

            z-index: 1;

            padding: 0.75rem 1rem;

            display: flex;
            flex-direction: column;
            justify-content: space-between;

            height: 55%;

            color: #000000;

            .balance-title {
                font-size: 20px;
                font-weight: 400;
            }

            .bottom {
                display: flex;
                flex-direction: row;
                align-items: flex-end;
                gap: 5rem;
                // justify-content: space-between;

                width: 100%;

                .balance {
                    font-size: 24px;
                    font-weight: 600;
                }

                .change {
                    // font-size: 1.5rem;
                    font-weight: 500;
                    font-size: 18px;

                    &[data-color="green"] {
                        color: var(--phone-color-green);
                    }
                }
            }
        }
    }

    .holdings {
        display: flex;
        flex-direction: column;
        gap: 1rem;

        width: 100%;

        .title {
            font-size: 22px;
            font-weight: 600;
            color: #ffffff;

            margin-left: 10%;
        }

        .items {
            display: flex;
            flex-direction: column;

            .item {
                display: flex;
                flex-direction: row;
                align-items: center;
                justify-content: space-between;

                padding: 1rem 10%;

                cursor: pointer;
                transition: all 0.2s ease-in-out;

                &:hover {
                    background-color: rgb(35, 35, 35);
                }

                .icon {
                    width: 2rem;
                    height: 2rem;
                }

                .coin {
                    .name {
                        font-size: 18px;
                        font-weight: 500;
                        color: #ffffff;
                    }

                    .symbol {
                        font-size: 14px;
                        font-weight: 400;
                        color: #6c757d;
                    }
                }

                .chart {
                    width: 5rem;
                }

                .amount {
                    .value {
                        font-size: 18px;
                        font-weight: 500;
                        color: #ffffff;
                    }

                    .asset {
                        font-size: 14px;
                        font-weight: 400;
                        color: #6c757d;
                    }
                }
            }
        }
    }
}
