$app-size: 64px;

.app-wrapper {
    width: 100%;

    // transform: translateZ(0);
    cursor: pointer;

    &[data-editmode="true"] {
        animation-delay: -0.22s;
        animation-duration: 0.3s;

        &:nth-child(2n) {
            animation-name: appJiggle;
            animation-iteration-count: infinite;
            transform-origin: 50% 10%;
        }

        &:nth-child(2n-1) {
            animation-name: appJiggle2;
            animation-iteration-count: infinite;
            animation-direction: alternate;
            transform-origin: 30% 5%;
        }
    }

    &.invisible {
        opacity: 0;
        cursor: default;
    }

    .image {
        position: relative;

        width: $app-size;
        aspect-ratio: 1 / 1;
        border-radius: 12.5px;

        background-position: center;
        background-size: contain;

        &[data-dummy="false"] {
            box-shadow: rgba(59, 59, 59, 0.3) 0px 0px 3px;
        }

        .notifications {
            position: absolute;
            top: -0.35rem;
            right: 0.25rem;

            transform: translate(50%, 0);

            width: 1.8rem;
            height: 1.8rem;
            border-radius: 50%;

            display: flex;
            align-items: center;
            justify-content: center;

            background-color: var(--phone-color-red);
            color: #ffffff;
            font-size: 16px;
        }

        .appDelete {
            position: absolute;
            top: -0.35rem;
            left: 0.25rem;
            width: 1.7rem;
            height: 1.7rem;
            background-color: rgba(167, 167, 167, 0.9);
            border-radius: 50%;

            transform: translate(-50%, 0);

            display: flex;
            justify-content: center;
            align-items: center;

            pointer-events: all;
            z-index: 99999999;

            .appDeleteBlur {
                position: absolute;

                width: 100%;
                height: 100%;
                border-radius: 50%;
            }

            i {
                position: absolute;

                color: #000000;
                font-size: 17px;
            }
        }
    }

    .name {
        font-size: 13px;
        line-height: 0px;
        padding-bottom: 10px;

        text-overflow: ellipsis;
        white-space: nowrap;

        color: white;
    }
}

.app {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    text-align: center;

    transition: filter 0.2s ease-in-out;

    &:hover:not([data-downloading="true"]) {
        filter: brightness(0.6);
    }

    // &[data-downloading="true"] {
    .progress-bar-container {
        position: absolute;
        height: 100%;
        width: 100%;
        z-index: 1;

        left: 0;
        right: 0;
        top: 0;
        bottom: 0;
        margin: auto;

        border-radius: 12.5px;
        background-color: rgba(0, 0, 0, 0.6);

        .progress-bar {
            position: absolute;

            width: 70%;
            aspect-ratio: 1 / 1;

            left: 0;
            right: 0;
            top: 0;
            bottom: 0;

            margin: auto;
            // left: 1.19rem;
            // bottom: 1.59rem;

            background-color: rgba(230, 230, 230, 0.2);
            border-radius: 50%;
            border: 4px solid rgba(255, 255, 255, 0.5);
        }
    }
    // .image {
    // filter: brightness(0.5);
    // }
    // }
}

.drag-app {
    position: absolute;
    top: 0;
    left: 0;
    transform: translate(-50%, -($app-size / 2));
    padding: 3px;

    pointer-events: none;
    user-select: none;

    z-index: 100;
}

