@import "../../../../styles/Colors.scss";

.maps-container {
    // margin-top: 0.25rem;
    height: 100%;
    width: 100%;

    display: flex;
    align-items: center;
    justify-content: center;

    position: relative;
    overflow: hidden;

    .map-wrapper {
        position: absolute;
        height: 115%;
        width: 115%;

        display: flex;
        align-items: center;
        justify-content: center;
        z-index: 9;

        .map {
            height: 100%;
            width: 100%;

            .leaflet-popup,
            .leaflet-popup-content-wrapper {
                background-color: var(--phone-color-primary);
                border-radius: 15px;
                color: var(--phone-text-primary);
            }

            .location-popup {
                display: flex;
                flex-direction: column;
                align-items: center;
                background-color: var(--phone-color-primary);

                .name {
                    font-size: 16px;
                    font-weight: 600;
                    color: var(--phone-text-primary);
                }

                .description {
                    font-size: 14px;
                    font-weight: 400;
                    font-style: italic;
                    color: var(--phone-text-secondary);
                }

                .button {
                    margin-top: 1rem;

                    width: 5rem;
                    height: 2rem;

                    background-color: var(--phone-color-blue);
                    color: #f7f7f6;
                    font-size: 15px;
                    font-weight: 500;

                    border-radius: 10px;

                    display: flex;
                    align-items: center;
                    justify-content: center;

                    cursor: pointer;
                }
            }
        }
    }

    .options {
        position: absolute;
        z-index: 400;

        top: 10rem;
        right: 2.5rem;

        display: flex;
        flex-direction: column;
        align-items: center;

        svg {
            padding: 0.8rem;
            background-color: #f7f7f6;

            font-size: 24px;
            color: #6c6c6c;

            cursor: pointer;
            transition: all 0.2s ease-in-out;

            &:first-child {
                border-bottom: 1px solid #6c6c6c;
                border-top-left-radius: 15px;
                border-top-right-radius: 15px;
            }

            &:last-child {
                border-bottom-left-radius: 15px;
                border-bottom-right-radius: 15px;
            }

            &:hover {
                filter: brightness(0.85);
            }
        }
    }

    .bottom-search {
        position: absolute;
        bottom: 0;

        z-index: 10;
        // background-color: #f7f7f6;
        background-color: #e9e9e9;

        width: 88%;

        border-radius: 15px;
        padding: 0.75rem 1.5rem;
        padding-bottom: 3.5rem;

        display: flex;
        flex-direction: column;
        align-items: center;

        .dot {
            // position: absolute;
            height: 0.3rem;
            width: 4rem;
            border-radius: 5px;
            cursor: pointer;

            background-color: rgba(0, 0, 0, 0.5);
        }

        .expanded {
            height: 100%;
            width: 100%;

            display: flex;
            flex-direction: column;
            align-items: center;

            margin-top: 1rem;

            .locations {
                width: 85%;
                border-radius: 7px;

                background-color: #ffffff;

                padding: 1rem 2rem;

                display: flex;

                display: grid;

                grid-template-columns: repeat(auto-fit, minmax(60px, 1fr));
                gap: 0.5rem 1.5rem;

                max-height: 17rem;
                overflow: auto;

                &::-webkit-scrollbar {
                    display: none;
                }

                .location {
                    display: flex;
                    flex-direction: column;
                    align-items: center;
                    gap: 0.15rem;

                    svg {
                        font-size: 33px;
                        color: var(--phone-color-blue);

                        border-radius: 50%;

                        background-color: rgba(145, 145, 145, 0.25);
                        padding: 0.8rem;

                        cursor: pointer;
                        transition: all 0.2s ease-in-out;

                        &:hover {
                            filter: brightness(0.7);
                        }
                    }

                    .name {
                        margin-top: 0.6rem;

                        overflow-wrap: break-word;
                        width: 100%;
                        text-align: center;

                        font-size: 18px;
                        font-weight: 500;
                    }

                    .action {
                        font-size: 16px;
                        font-weight: 400;
                        color: rgba(142, 142, 147);
                    }
                }
            }
        }
    }
}
