:root {
    --phone-frame-silver: 284;
    --phone-frame-silver: 22.5;
    --phone-frame-black: 215;
    --phone-frame-silver: 254;
}

.phone-frame {
    position: absolute;
    z-index: 1;

    width: 92%;
    height: 98%;

    border-radius: 60px;
    background-color: black;
    box-shadow: 0 0 0.1em 0.25em hsl(var(--phone-frame-silver), 20%, 25%), 0 0 0 0.4em hsl(var(--phone-frame-silver), 30%, 85%);
    z-index: 1;

    .frame-buttons {
        position: absolute;
        inset: calc(0.4em * -1);
        pointer-events: none;

        .left,
        .right {
            position: absolute;
            width: 0.333em;
            display: flex;
            flex-direction: column;
            align-items: stretch;
            gap: 1.5em;
        }

        .left {
            right: 100%;
            top: 10rem;

            .frame-button {
                cursor: pointer;
                pointer-events: all;

                &:nth-child(1) {
                    height: 3em;
                    margin-bottom: 0.5em;
                }
            }
        }

        .right {
            left: 100%;
            transform: scale3d(-1, 1, 1);
            top: 16rem;

            .frame-button {
                pointer-events: all;
                cursor: pointer;

                height: 6em;
            }
        }

        .frame-button {
            background: hsl(var(--phone-frame-silver), 20%, 95%);
            height: 4em;
            box-shadow: inset -0.15em 0 0.1em black, inset 0 0 0.1em hsl(var(--phone-frame-silver), 30%, 90%), inset 0 0.2em 0.1em hsl(var(--phone-frame-silver), 30%, 90%), inset 0 -0.2em 0.1em hsl(var(--phone-frame-silver), 30%, 90%), inset -0.1em 0.333em 0.1em rgba(black, 0.5), inset -0.1em -0.333em 0.1em rgba(black, 0.5);

            border-top-left-radius: 0.2em;
            border-bottom-left-radius: 0.2em;
        }
    }

    .phone-camera {
        display: flex;
        justify-content: center;
        align-items: center;
        height: 2rem;
        // width: 2rem;
        aspect-ratio: 1/1;
        border-radius: 50%;
        pointer-events: none;

        position: absolute;
        z-index: 9999999999999;

        right: 10rem;

        cursor: default;

        &:before {
            content: "";
            height: 33.3%;
            aspect-ratio: 1;
            border-radius: inherit;
            box-shadow: inset 0 0 0.25em #4c4da3;
            background: radial-gradient(#6667ac, transparent 50%) no-repeat 33.3% 10% / 75% 50%, radial-gradient(darken(#6667ac, 15), transparent 50%) no-repeat 60% 85% / 50% 50%;
            opacity: 0.75;
            background-color: #080928;
        }
    }

    .old-notch {
        position: absolute;
        z-index: 999;
        top: 0.62rem;

        // transform: translateZ(0);

        left: 0;
        right: 0;
        margin: 0 auto;
        width: 40%;
        height: 2.63rem;
        background-color: black;

        border-bottom-left-radius: 22px;
        border-bottom-right-radius: 22px;

        pointer-events: none;

        &:before,
        &:after {
            content: "";
            position: absolute;
            border: none;

            height: 10px;
            width: 20px;

            height: 20px;
            width: 20px;

            top: 0;
        }

        &:after {
            right: -20px;

            border-radius: 10px 0 0 0;

            box-shadow: -10px 0 0 0 black;
        }

        &:before {
            left: -20px;

            border-radius: 0 10px 0 0;

            box-shadow: 10px 0 0 0 black;
        }

        .phone-camera {
            left: 0.5rem;
            right: inherit;
        }
    }

    .phone-content {
        height: 100%;
        width: 100%;

        display: flex;
        align-items: center;
        justify-content: center;
    }
}
