:root {
    --homescreen-page-padding: 20px;
}

.homescreen-container {
    width: calc(100% - var(--homescreen-page-padding) * 2);
    height: calc(100% - var(--homescreen-page-padding) * 2);

    background-position: center;
    background-size: cover;
    padding: var(--homescreen-page-padding);

    padding-top: 4.5rem;
    z-index: 0;
}

.page-container {
    position: relative;
    width: 100%;
    height: 100%;

    > div {
        position: absolute;
        width: 100%;
        left: 0%;

        > div {
            position: absolute;
            width: 100%;
        }
    }
}

.bottom-container {
    position: absolute;
    bottom: 1rem;
    width: calc(100% - (var(--homescreen-page-padding) * 2));
    pointer-events: none;

    .favourites {
        pointer-events: all;
        backdrop-filter: blur(15px);

        background-color: var(--phone-color-opacity);
        padding: 1rem 0.5rem;

        border-radius: 30px;

        > div {
            width: 100%;
        }
    }
}

.app-grid {
    width: 100%;
    display: grid;
    grid-template-columns: repeat(4, minmax(0, 1fr));
    justify-content: space-evenly;
    user-select: none;
}

.page-indicator {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 84px;

    > div {
        display: flex;
        gap: 6px;
        padding: 8px 16px;
        border-radius: 12px;
        background: rgba(#fff, 0.3);

        pointer-events: all;
        cursor: pointer;

        z-index: 10;

        .indicator {
            content: " ";
            width: 7px;
            aspect-ratio: 1 / 1;
            border-radius: 100%;
            background: rgba(#fff, 0.3);

            &:hover {
                background: rgba(#fff, 0.5);
            }

            &.current {
                background: white;
            }
        }
    }
}

#left-navigation-area,
#right-navigation-area {
    position: absolute;
    top: 0;
    width: var(--homescreen-page-padding);
    height: 100%;
}

#left-navigation-area {
    left: 0;
}

#right-navigation-area {
    right: 0;
}

