.volume-master-wrapper {
    position: absolute;
    z-index: 999;
    left: 0.1rem;
    top: 17.75rem;

    height: 3rem;
    width: 4.5rem;

    .volume-master {
        height: 100%;
        width: 100%;

        display: flex;
        align-items: center;
        justify-content: center;

        position: relative;
        cursor: pointer;

        svg {
            position: absolute;
            bottom: -2.5rem;

            font-size: 25px;
            color: rgba(20, 20, 20, 0.5);
            cursor: pointer;

            pointer-events: none;

            &.white {
                color: rgba(255, 255, 255, 0.5);
            }
        }

        input {
            position: absolute;

            width: 200%;
            height: 100%;
            background-color: #ffffff;

            border-radius: 15px;

            -webkit-appearance: none;
            appearance: none;
            transform: rotate(-90deg);

            backdrop-filter: blur(5px);

            &:focus {
                outline: none;
            }

            &::-webkit-slider-thumb {
                -webkit-appearance: none;

                margin: 0 1rem;
                height: 1.75rem;
                width: 1.75rem;
                border-radius: 50%;
                background: transparent;
            }

            &::-webkit-slider-runnable-track {
                -webkit-appearance: none;
                box-shadow: none;
                border: none;
                background: transparent;
            }
        }
    }
}
